import { AcFunVideoAdapter } from './lib/adapter/acfunVideoAdapter'
import { BulletScreen } from './lib/bulletScreen'
let $ = require('jquery');

$(() => {
    let videoInfo = {
        videoId: pageInfo.videoId ? pageInfo.videoId : (bgmInfo.videoId ? bgmInfo.videoId : pageInfo.video.videos[0].videoId),
        coverImage: pageInfo.coverImage ? pageInfo.coverImage : (bgmInfo.image ? bgmInfo.image : pageInfo.video.videos[0].image)
    } //视频基本信息
    let a = new AcFunVideoAdapter($('#ACHtml5Player_player')[0]);
    let b = new BulletScreen($('#ACHtml5Player_bulletScreens')[0], $('#ACHtml5Player_player')[0]);
    a.load(videoInfo.videoId);
    b.load(videoInfo.videoId);
});